/**
 * Upload file s·ª≠ d·ª•ng XMLHttpRequest ƒë·ªÉ theo d√µi ti·∫øn tr√¨nh (Progress)
 * @param url ƒê∆∞·ªùng d·∫´n API (v√≠ d·ª•: /api/upload)
 * @param formData D·ªØ li·ªáu form ch·ª©a file
 * @param onProgress Callback nh·∫≠n % ho√†n th√†nh (0 -> 100)
 */
import type { MessageCreate } from '@/types/Message';

export interface UploadSuccessResponse {
  success: true;
  link: string;
  data: MessageCreate;
  _id?: string;
  saved?: boolean;
}

export interface UploadErrorResponse {
  success: false;
  message: string;
}

export type UploadResponse = UploadSuccessResponse | UploadErrorResponse;

export const uploadFileWithProgress = (
  url: string,
  formData: FormData,
  onProgress: (percent: number) => void,
): Promise<UploadResponse> => {
  return new Promise((resolve) => {
    const xhr = new XMLHttpRequest();

    // 1. L·∫Øng nghe s·ª± ki·ªán ti·∫øn tr√¨nh upload (quan tr·ªçng nh·∫•t)
    xhr.upload.onprogress = (event) => {
      if (event.lengthComputable) {
        const percentComplete = (event.loaded / event.total) * 100;
        onProgress(percentComplete); // G·ªçi callback ƒë·ªÉ c·∫≠p nh·∫≠t UI
      }
    };

    // 2. M·ªü k·∫øt n·ªëi POST
    xhr.open('POST', url);

    // 3. X·ª≠ l√Ω khi ho√†n t·∫•t
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        try {
          // Parse k·∫øt qu·∫£ tr·∫£ v·ªÅ t·ª´ server (JSON)
          const response = JSON.parse(xhr.responseText) as UploadResponse;
          resolve(response);
        } catch {
          resolve({ success: false, message: 'Ph·∫£n h·ªìi t·ª´ server kh√¥ng ph·∫£i JSON h·ª£p l·ªá' });
        }
      } else {
        try {
          const parsed = JSON.parse(xhr.responseText) as { message?: string };
          const msg =
            typeof parsed?.message === 'string' && parsed.message.trim()
              ? parsed.message
              : xhr.statusText || `Upload th·∫•t b·∫°i (${xhr.status})`;
          resolve({ success: false, message: msg });
        } catch {
          const msg = xhr.statusText || `Upload th·∫•t b·∫°i (${xhr.status})`;
          resolve({ success: false, message: msg });
        }
      }
    };

    // 4. X·ª≠ l√Ω l·ªói m·∫°ng
    xhr.onerror = () => {
      resolve({ success: false, message: 'L·ªói m·∫°ng (Network Error)' });
    };

    // 5. G·ª≠i d·ªØ li·ªáu ƒëi
    xhr.send(formData);
  });
};
